.side-bar__position {
	padding: 5px 5px 5px 5px;
	border-bottom: 1px solid #f97e76;
	position: relative;
}
.view {
	&__list {
		float: right;
		@include text-hide();
	}
	&__item {
		@include inline-block(top);
	}
}
.view__link {
	width: 29px;
	height: 29px;
	display: block;
	position: relative;
	&:before {
		content: '';
		width: 19px;
		height: 19px;
		display: block;
		@include transform-to-center();
		background-repeat: no-repeat;
		background-image: url('/../images/spritesheet.png');
	}
}
.view-1:before {
	width: nth($view-1, 5); height: nth($view-1, 6);
	background-position: nth($view-1, 3) nth($view-1, 4);
}
.view__item-1:hover .view-1:before {
	width: nth($view-1-hover, 5); height: nth($view-1-hover, 6);
	background-position: nth($view-1-hover, 3) nth($view-1-hover, 4);
}
.view-2:before {
	width: nth($view-2, 5); height: nth($view-2, 6);
	background-position: nth($view-2, 3) nth($view-2, 4);
}
.view__item-2:hover .view-2:before {
	width: nth($view-2-hover, 5); height: nth($view-2-hover, 6);
	background-position: nth($view-2-hover, 3) nth($view-2-hover, 4);
}
.position {
	&__top {
		@include clearfix();
	}
	&__label {
		font: 14px/15px $roboto_light;
		color: #2e3641;
    	letter-spacing: 1px;
    	padding: 13px 15px;
    	display: block;
    	float: left;
	}
	&__top {

	}
	&__bottom {
		padding: 25px 16px;
		@include clearfix();
	}
	$boxSize: 103px;
	&__list {
		float: left;
		@include text-hide();
		width: $boxSize;
		height: $boxSize;
		border-top: 1px solid #9eb2c0;			

	}
	&__item {
		@include inline-block(top);
		border-right: 1px solid #9eb2c0;
		border-bottom: 1px solid #9eb2c0;
		&:nth-child(3n+1) {
			border-left: 1px solid #9eb2c0;
		}
	}

	&__link {
		width: ($boxSize - 4) / 3;
		height: ($boxSize - 4) / 3;
		display: block;
		position: relative;
		background: #dbe1e8;
		&:hover {
			background: #f97e76;
		}
	}
}

