.axis__up,
.axis__down,
 {
	float: right;
	@include clearfix();
	@include text-hide();
}
.axis__down {
	margin-top: 13px;
}
.axis__x-a,
.axis__y-a  {
	width:45px;
	height: 45px;
	// display: block;
	// @include inline-block();
	float: left;
	position: relative;
}
.axis__x-a:before,
.axis__y-a:before {
	content: '';
	display: block;

	position: absolute;
	background-repeat: no-repeat;
    background: url('/../images/spritesheet.png');
}
 .axis__x-a:before{
 	right: 0;
 	top: 50%;
 	transform: translate(-50%, -50%);
	width: nth($arrows-vertical, 5);
    height: nth($arrows-vertical, 6);
    background-position: nth($arrows-vertical, 3) nth($arrows-vertical, 4);
}
.axis__y-a:before {
	right: 0;
 	top: 50%;
 	transform: translate(0, -50%);
    width: nth($arrows-horizontal, 5);
    height: nth($arrows-horizontal, 6);
    background-position: nth($arrows-horizontal, 3) nth($arrows-horizontal, 4);
}
.axis__x-b,
.axis__y-b {
	width:45px;
	height: 45px;
	display: block;
	float: left;
	margin-left: 6px; 
}
.axis__x-input,
.axis__y-input {
	width:45px;
	height: 45px;
	border: 1px solid #ccc;
	@include border-radius(5px);
	outline: none;
	padding: 8px;
	font-family: "Segoe UI";
	font-size: 24px;
	color: #444a53;
}
.axis__x-c,
.axis__y-c {
	width:13px;
	height: 45px;
	display: block;
	float: left;
	margin-left: 6px;
	position: relative;
}
.axis__link { position: absolute }
.arrow__x-up,
.arrow__y-up { top: 0 }
.arrow__x-down,
.arrow__y-down { bottom: 0 }
.arrow__x-up,
.arrow__x-down,
.arrow__y-up,
.arrow__y-down {
	width:13px;
	height: 8px;
	&:before {
	    content: '';
	    display: block;
	    position: absolute;
	    background-repeat: no-repeat;
	    @include transform-to-center();
	    background: url('/../images/spritesheet.png');
	}
}
.arrow__x-up:before,
.arrow__y-up:before {
    width: nth($st-sa, 5);
    height: nth($st-sa, 6);
    background-position: nth($st-sa, 3) nth($st-sa, 4);
}
.arrow__x-down:before,
.arrow__y-down:before {
    width: nth($st-sd, 5);
    height: nth($st-sd, 6);
    background-position: nth($st-sd, 3) nth($st-sd, 4);
}
.axis__x-item_up:hover .arrow__x-up:before {
	background-position: nth($st-sa-hover, 3) nth($st-sa-hover, 4);
}
.axis__x-item_down:hover .arrow__x-down:before {
	background-position: nth($st-sd-hover, 3) nth($st-sd-hover, 4);
}
.axis__y-item_up:hover .arrow__y-up:before {
	background-position: nth($st-sa-hover, 3) nth($st-sa-hover, 4);
}
.axis__y-item_down:hover .arrow__y-down:before {
	background-position: nth($st-sd-hover, 3) nth($st-sd-hover, 4);
}
